        </router-link>
    </div>
    
</template>

<script>
export default {
    name:'CityHeader'
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
.header
    position:relative
    overflow: hidden
    height: .86rem
    line-height: .86rem
    text-align: center
    color: #fff
    background: $bgColor
    font-size:.32rem
    .header-back
        position: absolute
        top:0
        left:0
        width: .67rem
        text-align:center
        font-size: .6rem
        color:#fff
</style>



