<template>
<div>
  <div class="banner" @click="handleClick">
      <img class="banner-img" :src="bannerImg">
      <div class="banner-info">
          <div class="banner-number">
              <span class="iconfont">&#xe61e;</span> &nbsp;2
          </div>
          <div class="banner-title">{{sightName}}</div>
      </div>
  </div>
  <fade-animation>
    <common-gallary   :imgs="gallaryImgs" 
                    v-show="showGallary" 
                    @close="close">
    </common-gallary>
    </fade-animation>
</div>
</template>

<script>
import CommonGallary from 'common/gallary/Gallary'
import FadeAnimation from 'common/fade/Fade'

export default {
  name: "DetailBanner",
  props:{
      sightName:String,
      bannerImg:String,
      gallaryImgs:Array
  },
  data(){
      return{
            showGallary:false
      }
  },
  methods:{
      handleClick(){
          this.showGallary = true
      },
      close(){
          this.showGallary = false
      }
  },
  components:{
      CommonGallary,
      FadeAnimation
  }
};
</script>

<style lang="stylus" scoped>
.banner
    position: relative
    height:0
    overflow: hidden
    padding-bottom: 65.75%
    .banner-img
        width 100%
    .banner-info
        z-index:99
        display:flex
        position: absolute
        left:0
        right:0
        bottom:0
        line-height:.6rem
        color:#fff
        background-image : linear-gradient(top,rgba(0,0,0,0),rgba(0,0,0,0.8))
        .banner-title
            flex:1
        .banner-number
            padding : 0 .2rem
            line-height:.4rem
            height:.4rem
            border-radius: .2rem
            background: rgba(0 ,0 ,0 ,.6)
            margin: .1rem .05rem 0 .05rem 

</style>



