<template>
    <div>
        <router-link to = "/">
        <div class="header-abs iconfont" v-show="showAbs">
         &#xe658;
        </div>
        </router-link>
        <div class="header-fixed" v-show="!showAbs" :style="opacityStyle">
            <router-link to = "/">
                <div class="iconfont header-back"> &#xe658; </div>
            </router-link>
            景点详情
        </div>
    </div>    
</template>

<script>

export default {
    name: 'DetailHeader',
    data(){
        return{
            showAbs:true,
            opacityStyle:{
                opacity:0
            }
            }
    },
    methods:{
        handleScroll(){
            console.log("aa")
            const top = document.documentElement.scrollTop
            if (top > 60){
                let opacity = top/140
                opacity = opacity > 1 ? 1:opacity
                this.opacityStyle = {opacity}
                this.showAbs = false
            } else {
                this.showAbs = true
            }
        }
    },
    created () {
        window.addEventListener('scroll', this.handleScroll)
    },
    beforeDestroy () {
        window.removeEventListener('scroll', this.handleScroll)
    }
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
    .header-abs
        position: absolute
        left : .2rem
        top: .2rem
        width .8rem
        height .8rem
        border-radius: .4rem
        background: rgba(0,0,0,.4)
        line-height .8rem
        text-align  center
        font-size: .6rem
        color:#fff
    .header-fixed
        z-index 999
        position:fixed
        top 0
        left 0
        right 0
        // overflow: hidden
        height: .86rem
        line-height: .86rem
        text-align: center
        color: #fff
        background: $bgColor
        font-size:.32rem
        .header-back
            position: absolute
            top:0
            left:0
            width: .67rem
            text-align:center
            font-size: .6rem
            color:#fff
</style>



