<template>
  <div class="container" @click="handleClick">
    <div class="wrapper">
      <swiper :options="swiperOptions" v-if="showImgs">
        <!-- slides -->
        <swiper-slide v-for="(item,index) of imgs" :key="index" >
          <img class="gallary-img" :src="item">
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>
<script>
export default {
  name: "CommonGallary",
  props:{
      imgs:{
          type:Array
      }
  },
  methods:{
      handleClick(){
          this.$emit('close')
      }
  },
  computed:{
    showImgs(){
      return this.imgs.length
    }
  },
  data(){
      return{
      swiperOptions:{
          autoplay:false,
          loop:true,
          pagination:".swiper-pagination",
          paginationType:'fraction',
          observeParents:true,
          observer: true
      }
      }
  }
};
</script>
<style lang="stylus" scoped>
.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 999;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #000;
 

  .wrapper {
    overflow: hidden;
    height: 0;
    width: 100%;
    padding-bottom: 100%;
    .gallary-img{
        width 100%
    }
    .swiper-pagination{
        color:#fff
        bottom: 0
    }
  }
}
</style>



